export default {
	data() {
		return {
			isbind: false,//用于检测用户是否绑定了手机号码false未绑定,true已绑定页面效果不同
			disabled: true,
			loading: false,
			phone: "",
			checknum: "",
			codetime: 0
		}
	},
	onLoad(e) {
		if (e.phone !== "false" && e.phone && e.phone !== 'null') {
			this.isbind = true;
			this.phone = e.phone;
		}
		console.log(this.user.userinfo)
	},
	watch: {
		phone(val) {
			this.OnBtnChange();
		},
		checknum(val) {
			this.OnBtnChange();
		}
	},
	methods: {
		// 验证手机号码
		isPhone() {
			let mPattern = /^1[34578]\d{9}$/;
			return mPattern.test(this.phone);
		},
		// 改变按钮状态
		OnBtnChange() {
			if (this.phone && this.checknum) {
				this.disabled = false;
				return;
			}
			this.disabled = true;
		},
		async getCheckNum() {//请求服务器发送验证码
			if (this.codetime > 0) return;
			// 验证手机号合法性
			if (!this.isPhone()) {
				return uni.showToast({
					title: '请输入正确的手机号码',
					icon: "none"
				});
			}
			// 请求服务器，发送验证码
			let [err, res] = await this.$http.post('/user/sendcode', {
				phone: this.phone
			});
			// 请求失败处理
			console.log(res)
			this.$http.errorcheck(err, res);
			if (res.data.errorCode === 30001) return;
			// 发送成功，开启倒计时
			uni.showToast({
				title:res.data.msg,
				icon:"none"
			})
			this.codetime = 60;
			let timer = setInterval(() => {
				this.codetime--;
				if (this.codetime < 1) {
					clearInterval(timer);
					this.codetime = 0;
				}
			}, 1000);
		},
		async submit() {
			let [err, res] = await this.$http.post('/user/bindphone', {
				phone: this.phone,
				code: this.checknum
			}, {
				token: true,
				checkToken: true,
			});
			// let [err,res]=uni.request({
			// 	url: '',
			// 	method: 'post',
			// 	data: {
			// 		phone:this.phone,
			// 		code:this.checknum
			// 	},
			// 	header:{
			// 		'token':uni.getStorageSync('token')
			// 	}
			// });
			console.log(res)
			// 请求失败处理
			if (!this.$http.errorcheck(err, res)) return;
			// 成功
			this.isbind = true;//用于检测用户是否绑定手机号码
			this.disabled = false;
			uni.showToast({
				title: '绑定成功！'
			});
			// 修改状态，保存缓存
			let data = res.data.data;
			let pic=uni.getStorageSync('userinfo').userpic
			if (typeof data === 'boolean') {
				this.user.userinfo.phone = this.phone;
			} else {
				this.user.userinfo = this.user.__formatUserinfo(data);
			}
			this.user.userinfo.userpic=pic
			uni.setStorageSync("userinfo", this.user.userinfo);
		}
	}
}
